import {Button, TextField} from "@mui/material";
import {useState} from "react";


//        
import CustomConfirm from "../../components/CustomConfirm";
import CustomAlert from "../../components/CustomAlert";
import CustomInput from "../../components/CustomInput";
import axios from "axios";
import NewClass from "../../components/NewClass";

/**
 * @description:
 *   
* */
export default function UseModal() {

    const [confirmVisible, setConfirmVisible] = useState(false);
    const [alertVisible, setAlertVisible] = useState(false);
    const [className, setClassName] = useState("");
    const [classDescription, setClassDescription] = useState("");
    const [alertTitle, setAlertTitle] = useState("");
    const onSetClassName = (className) => {
        setClassName(className);
    }

    const onSetClassDescription = (classDescription) => {
        setClassDescription(classDescription);
    }

    /**
     * @description : Confirm  
     * */
    const openConfirm = () => {
        setConfirmVisible(true);
    };

    /**
     * @description :     
     * */
    const clickBtn1 = () => {
        setConfirmVisible(false);
    };

    /**
     * @description :     
     * */
    const clickBtn2 = () => {
        if(className==''){
            setAlertTitle("  .");
            openAlert();
            return;
        }
        if(classDescription==''){
            setAlertTitle("  .");
            openAlert();
            return;
        }
        axios({
            method:'post',
            url:'/myclass/newclass',
            data:{
                "class_name": className,
                "class_description": classDescription,
            },
        }).then(res=>{
            console.log(res);
            setClassName('');
            setClassDescription('')
        })
        setConfirmVisible(false);
    };

    /**
    * @description : Alert  
    * */
    const openAlert = () => {
        setAlertVisible(true);
    };

    /**
     * @description : Alert  
     * */
    const closeAlert = () => {
        setAlertVisible(false);
    };


    return (
        <>
            <Button
                onClick={openConfirm}
                className="whitespace-nowrap">
                    
            </Button>
            <CustomConfirm
                id={9} // CustomConfirm id  
                // props   (title, content, btn1Text,  btn2Text)
                content={
                    <NewClass
                        label={"Class name"}
                        placeholder={""}
                        updateClassName={onSetClassName}
                        updateClassDescription={onSetClassDescription}
                    />
                }
                openConfirm={confirmVisible}
                clickBtn1={clickBtn1}
                clickBtn2={clickBtn2}
            />
            <Button
                onClick={openAlert}
                className="whitespace-nowrap">
                    
            </Button>
            <CustomAlert
                //id={5} // CustomAlert id  
                // props   (title, content, btnText)
                title={alertTitle}
                openAlert={alertVisible}
                closeAlert={closeAlert}
            />
        </>
    );
}
