import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import ConfirmContent from "./ConfirmContent";

/**
 * @description :
 *       
 * props id   Confirm data  
 * */
export default function CustomConfirm(props) {
    let title = "";
    let content = "";
    let btn1Text = "";
    let btn2Text = "";

    ConfirmContent.map((e)=>{
        if(e.id === props.id) {
            title = e.title;
            content = e.content;
            btn1Text = e.btn1Text;
            btn2Text = e.btn2Text;
        }
    })

    const clickBtn1 = () => {
        props.clickBtn1();
    };

    const clickBtn2 = () => {
        props.clickBtn2();
    };

    return (
        <>
            <Dialog
                open={props.openConfirm}
                onClose={props.closeConfirm}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {props.title || title ||' '}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {/* content props     . */}
                        {props.content || content || ' '}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={clickBtn1}>
                        {props.btn1Text || btn1Text || ''}
                    </Button>
                    <Button onClick={clickBtn2} autoFocus>
                        {props.btn2Text || btn2Text || ''}
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    );
}

