import {IconButton, TextField, Typography} from "@mui/material";
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

export default function CustomInput(props) {

    /**
     * @description :
     * input      .
     *   prop   .
     *
    * */
    const updateValue = (e) => {
        props.updateValue(e.target.value);
    }

    return (
        <div className="flex flex-col">
            <div className="flex">
                <TextField id="standard-basic"
                           label={props.label}
                           value={props.value}
                           type={props.type}
                           variant="standard"
                           placeholder={props.placeholder}
                           onChange={updateValue}
                />

                {
                    //       
                    /**
                    * @TODO :    
                    * */
                    props.isPassword &&
                    <div>
                        <IconButton>
                            { props.passwordVisible ? <VisibilityIcon/> : <VisibilityOffIcon/> }
                        </IconButton>
                    </div>
                }

            </div>

            {
                props.captionVisible &&
                <Typography variant="caption" color={props.captionColor}>{props.captionText}</Typography>
            }
            {
                /**
                 * @TODO :   
                 * */
                props.timerVisible &&
                    <Typography variant="caption" color={props.captionColor}>   : 03:00</Typography>
                }
        </div>
        )
}
